<template>
    <div class="drillResult">
        <div class="search">
            <el-input placeholder="请输入演练名称" v-model="search.keyword" clearable size="small"></el-input>
            <el-select clearable size="small" placeholder="请选择事件类型" v-model="search.scene">
                <el-option v-for="item in sceneList" :label="item.dictionary_name" :value="item.dictionary_value" :key="item.dictionary_value"></el-option>
            </el-select>
            <el-button size="small" type="primary" plain icon="el-icon-search" @click="serchList()">查询</el-button>
        </div>
        <div class="home_table">
            <el-table :data="tableData" border element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%">
                <template v-for="(item, index) in tableHead">
                    <el-table-column :prop="item.prop" v-if="item.prop=='create_time'" align="center" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true">
                        <template slot-scope="scope">
                            {{scope.row.create_time+"-"+scope.row.end_time}}
                        </template>
                    </el-table-column>
                    <el-table-column :prop="item.prop" v-else-if="item.prop=='person'" align="center" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true">
                        <template slot-scope="scope">
                            <el-link :underline="false" type="primary" @click="viewTask(scope.row)">查看任务</el-link>
                        </template>
                    </el-table-column>
                    <el-table-column :prop="item.prop" v-else align="center" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true"></el-table-column>
                </template>
                <el-table-column prop="address" label="操作" fixed="right" width="100">
                    <template slot-scope="scope">
                        <el-button @click="check(scope.row)" type="text" size="small">查看详情</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="pagination">
            <el-pagination background :current-page.sync="page" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
        </div>
        <el-dialog title="人员任务" :visible.sync="dialogVisible" v-dialogDrag width="700px" :close-on-click-modal="false">
            <span v-if="String(task.person_total).length>0">参与人员：{{task.person_total}}</span>
            <span v-if="String(task.person_confirm).length>0">演练确定人员：{{task.person_confirm}}</span>
            <div class="home_table">
                <el-table :data="tableDataTask" border element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%">
                    <template v-for="(item, index) in tableHeadTask">
                        <el-table-column :prop="item.prop" v-if="item.prop=='status'" align="center" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true">
                            <template slot-scope="scope">
                                {{scope.row.status==1?"已确定":"未确定"}}
                            </template>
                        </el-table-column>
                        <el-table-column :prop="item.prop" v-else align="center" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true"></el-table-column>
                    </template>
                </el-table>
            </div>
        </el-dialog>
        <el-dialog title="查看详情" :visible.sync="visible" v-dialogDrag width="700px" :close-on-click-modal="false">
            <el-descriptions :column='1'>
                <el-descriptions-item label="演练名称">{{checkInfo.title}}</el-descriptions-item>
                <el-descriptions-item label="事件类型">{{checkInfo.scene_name}}</el-descriptions-item>
                <el-descriptions-item label="时间">{{checkInfo.create_time+'-'+checkInfo.end_time}}</el-descriptions-item>
                <el-descriptions-item label="参与人员">{{String(checkInfo.person_total).length>0? checkInfo.person_total+'个':''}}</el-descriptions-item>
                <el-descriptions-item label="演练确定人员">{{String(checkInfo.person_confirm).length>0? checkInfo.person_confirm+'个':''}}</el-descriptions-item>
                <template v-for="(item,index) in checkInfo.warn_list">
                    <el-descriptions-item :key="index" label="预警事件">{{item.warn_type_name}}</el-descriptions-item>
                    <el-descriptions-item :key="index" label="预警次数">{{String(item.warn_count).length>0? item.warn_count+'次':''}}</el-descriptions-item>
                    <el-descriptions-item label="预警文件" v-for="(file,fileIndex) in item.list" :key="fileIndex">
                        <el-image style="width: 100px; height: 100px;margin-right:5px;" :src="file.image_url" :preview-src-list="[file.image_url]">
                        </el-image>
                    </el-descriptions-item>
                </template>
            </el-descriptions>
        </el-dialog>
    </div>
</template>

<script>
import {
    drillResultList,
    drillResultView,
    drillResultInfo,
    drillResultTaskInfo,
} from '@/api/index';
export default {
    data() {
        return {
            total: 0,
            page: 1,
            pageSize: 15,
            tableHeight: window.innerHeight - 180,
            dialogVisible: false,
            visible: false,
            title: '',//新增编辑标题
            search: {
                keyword: '',
                scene: '',
            },
            formData: {
                video: {},
                image: {},
            },
            equipment: [],
            tableHead: [
                { title: '序号', prop: 'id', minWidth: '100', },
                { title: '演练名称', prop: 'title', minWidth: '120', },
                { title: '事件类型', prop: 'scene_name', minWidth: '120', },
                { title: '时间', prop: 'create_time', minWidth: '200', },
                { title: '人员任务', prop: 'person', minWidth: '180', },
                { title: '预警次数', prop: 'warn_count', minWidth: '120', },
            ],
            tableHeadTask: [
                { title: '序号', prop: 'id', minWidth: '80', },
                { title: '参与人员', prop: 'real_name', minWidth: '100', },
                { title: '人员任务', prop: 'task', minWidth: '100', },
                { title: '演练确定', prop: 'status', minWidth: '100', },
            ],
            tableData: [],
            tableDataTask: [],
            sceneList: [],
            baseUrl: '',
            task: {},
            checkInfo: {},
        };
    },
    mounted() {
        this.baseUrl = process.env.UPLOADER_URL;
        this.getList();
        this.resView();
    },
    methods: {
        serchList() {
            this.page = 1;
            this.pageSize = 15;
            this.getList()
        },
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.page = 1;
            this.pageSize = newSize;
            this.getList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.page = newPage;
            this.getList();
        },
        async getList() {
            let data = {
                curr: this.page,
                pageSize: this.pageSize,
                keyword: this.search.keyword,
                scene: this.search.scene,
            }
            const res = await this.axios.post(drillResultList, data);
            if (res.data.code == 1) {
                this.tableData = res.data.data.data;
                this.total = res.data.data.total;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async resView() {
            const res = await this.axios.post(drillResultView);
            if (res.data.code == 1) {
                this.sceneList = res.data.data.scene_list;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        //查看任务
        viewTask(row) {
            this.dialogVisible = true;
            this.tableDataTask = [];
            this.task = {};
            this.$nextTick(() => {
                this.taskInfo(row.id)
            })
        },
        async taskInfo(id) {
            const res = await this.axios.post(drillResultTaskInfo, { id: id });
            if (res.data.code == 1) {
                this.tableDataTask = res.data.data.list;
                this.task = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        check(row) {
            this.visible = true;
            this.checkInfo = {};
            this.$nextTick(() => {
                this.getInfo(row.id)
            })
        },
        async getInfo(id) {
            const res = await this.axios.post(drillResultInfo, { id: id });
            if (res.data.code == 1) {
                this.checkInfo = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        videoClick(e) {
            window.open(e, '_blank')
        },
    },
};
</script>

<style lang="scss" scoped>
.search {
    display: flex;
    padding: 12px 25px;
    background: #fff;
    .el-select {
        margin-right: 24px;
    }
    .el-input {
        width: auto;
        margin-right: 24px;
    }
    .el-cascader {
        margin-right: 24px;
    }
    .add {
        flex: 1;
        .btn_add {
            float: right;
        }
        .el-input {
            width: 500px;
        }
    }
}
</style>


